<template>
  <v-container
    id="data-tables"
    class="fill-height align-start"
    fluid
    tag="section"
  >
    <v-container
      fluid
    >
      <ik-data-table
        :model="model"
        icon="mdi-table-chair"
        :entity-name=" $t('桌子管理')"
      >
        <template v-slot:filterRight>
          <v-btn
            class="ma-4"
            color="red darken-2"
            dark
            outlined
            @click="addMultipleTable"
          >
            {{ $t('批量添加桌子') }}
            <v-icon
              dark
              right
            >
              mdi-plus
            </v-icon>
          </v-btn>
          <v-dialog
            v-model="dialog"
            max-width="500px"
          >
            <v-card>
              <v-card-title>
                <span class="headline">{{ $t('批量创建桌子') }}</span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col
                      cols="12"
                      md="6"
                      sm="6"
                    >
                      <v-select
                        v-model="editedItem.sectionId"
                        :items="sectionList"
                        item-text="name"
                        item-value="id"
                        :label="$t('sectionName')"
                        required
                      />
                    </v-col>
                    <v-col
                      cols="12"
                      md="6"
                      sm="12"
                    >
                      <v-text-field
                        v-model="editedItem.tableAmount"
                        :label="$t('tableCount')"
                        required
                      />
                    </v-col>
                    <v-col
                      cols="12"
                      md="6"
                      sm="12"
                    >
                      <v-text-field
                        v-model="editedItem.seatCount"
                        :label="$t('seatCount')"
                      />
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
              <v-card-actions>
                <v-spacer/>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="close"
                >
                  {{ $t('Cancel') }}
                </v-btn>
                <v-btn
                  color="blue darken-1"
                  @click="save"
                >
                  {{ $t('Save') }}
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </template>
        <template v-slot:item.action="{item}">
          <v-icon
            class="mr-2"
            small
            @click="goNext(item)"
          >
            mdi-arrow-right
          </v-icon>
        </template>
      </ik-data-table>
    </v-container>
  </v-container>
</template>

<script>

  import { IkDataTable } from 'innerken-utils'
  import Table from '../../model/Table'
  import Section from '../../model/Section'
  import { SetMultipleTable } from '../../components/Utlis/api'
  import Utils from '../../components/Utlis/Utils'

  export default {
    components: {
      IkDataTable,
    },
    data: function () {
      return ({
        model: Table,
        sectionModel: Section,
        sectionList: [],
        dialog: false,
        editedItem: {
          sectionId: '',
          tableAmount: '',
          seatCount: '4',
        },
      })
    },
    created () {
      this.getSectionList()
    },
    methods: {
      addMultipleTable () {
        this.dialog = true
      },
      save () {
        SetMultipleTable(this.editedItem.sectionId, this.editedItem.tableAmount, this.editedItem.seatCount).then((res) => {
          Utils.toast('Successful!')
          this.close()
        })
      },
      close () {
        this.dialog = false
      },
      async getSectionList () {
        this.sectionList = await Section.getList()
      },
      goNext (item) {
        this.$router.push({
          name: 'Tabellendetailseite',
          params: { id: item.id },
        })
      },
    },
  }
</script>
